.entrar-caja {
    opacity: 0;
    animation: aparecer 0.5s 0.5s 1 forwards linear;
}

@keyframes aparecer {
    from {opacity: 0;}
    to {opacity: 1;}
}

.cont-titulo-caja-agregar {
    display: flex;
    justify-content: space-around;
    align-items: center;
}